* {
  box-sizing: border-box;
  --cor-blue: #00acdc;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav {
  align-items: center;
  display: block;
}

.nav__list {
  gap: 20px;
  list-style: none;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  padding: 0;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link.is-active {
  color: var(--cor-blue);
}

.is-active:after {
  content: "";
  background-color: var(--cor-blue);
  border-radius: 10px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__link:hover {
  color: var(--cor-blue);
}
